import React, { Component } from 'react';
import draw from './vis';
import mkSvg from '../../../util/mkSvg';
import * as d3 from 'd3';



const preset = {
    width: 500,
    height: 600,
    wrapperClassName: "mapWrapper",
    svgId: "mapChart",
    ChartClassName: "mapChart"
}


export default class MapDraw extends Component {
    constructor(props) {
        super(props)
        this.state = { preset };
    }
    show = () => {
        mkSvg(this.state.preset);
        const svg = d3.select(`#${this.state.preset.svgId}`)
        svg.append("g")
            .attr("class", "hotspots")
        draw(this.props, this.state.preset.width, this.state.preset.height)
    }

    componentDidMount() {
        this.show()
    }

    componentDidUpdate(preProps) {
        if (preProps !== this.props) {
            draw(this.props, this.state.preset.width, this.state.preset.height);
        }
    }

    render() {
        return (
            <div className={this.state.preset.wrapperClassName} style={{ width: 80 + "%" }}>
            </div>
        )
    }
}