import * as d3 from 'd3';

var mkSvg = (preset) => {

    var { width, height, wrapperClassName, svgId, ChartClassName } = preset;

    const margin = {
        top: 10,
        bottom: 10,
        right: 10,
        left: 10,
    };

    const svg = d3.select(`.${wrapperClassName}`)
        .append("svg")
        .attr("id", svgId)
        .attr('viewBox', `0 0 ${width} ${height}`)
        .attr("width", "100%")
    width = width - margin.right - margin.left;
    height = height - margin.top - margin.bottom;

    if (ChartClassName) {
        svg.append("g")
            .attr("transform", "translate(" + margin.left + "," + margin.top + ")")
            .attr("class", ChartClassName);
    }




}
export default mkSvg