import React, { Component } from 'react';
import Iframe from 'react-iframe';
import "./SideChart.css";
import Chart from './Chart/Chart';


const preset = {
    width: 700,
    height: 750,
    wrapperClassName: "sideChartWrapper",
    svgId: "sideChart",
    ChartClassName: null,
}


class SideChart extends Component {
    constructor(props) {
        super(props);
        this.state = {
            preset,
        }
    }

    render() {
        var { region, plot, dailyPrice, beijing_area_stat } = this.props;

        if (region === "All") {

            if (plot === "lists") {
                return (
                    <div className={this.state.preset.wrapperClassName} style={{ height: 500, width: 100 + "%", padding: 20 }}>
                        <Iframe url='https://flo.uri.sh/visualisation/2211617/embed' width="100%" height="100%" id="room_type" >
                        </Iframe>
                    </div >
                )
            } else if (plot === 'rent') {
                return (
                    <div className={this.state.preset.wrapperClassName} style={{ height: 500, width: 100 + "%", padding: 20 }}>
                        <Iframe url='https://flo.uri.sh/visualisation/2129770/embed' width="100%" height="100%" id="daily_rent" >
                        </Iframe>
                    </div>
                )
            } else if (plot === 'price') {
                return (
                    <div className={this.state.preset.wrapperClassName} style={{ height: 500, width: 100 + "%", padding: 20 }}>
                        <Iframe url='https://flo.uri.sh/visualisation/2129151/embed' width="100%" height="100%" id="housing_price" >
                        </Iframe>
                    </div >
                )
            }
        } else {
            return (
                <Chart preset={this.state.preset} region={region} plot={plot} dailyPrice={dailyPrice} beijing_area_stat={beijing_area_stat}></Chart>
            )
        }

    }
}
export default SideChart;

