import * as d3 from 'd3';
import * as topojson from 'topojson';
import d3Tip from 'd3-tip';
import anime from 'animejs';

const projection = d3.geoAlbers().rotate([-90, 0]).scale(1).translate([0, 0]);
const path = d3.geoPath().projection(projection);

let t = d3.transition().duration(1000).ease(d3.easeQuadOut);

const margin = {
  top: 10,
  bottom: 10,
  right: 10,
  left: 10,
};

export default function draw(props, width, height) {
  width = width - margin.right - margin.left;
  height = height - margin.top - margin.bottom;

  const geoData = props.geoData;
  const beijing_area_stat = props.beijing_area_stat;
  const hotSpot = props.hotSpot;

  // eventHandler
  var region = props.region;
  var plot = props.plot;
  var handleSpot = props.handleSpot;

  var areaById = d3.map();

  beijing_area_stat.map(d => {
    d.num_spot = +d.num_spot;
    d.mean_reviews = +d.mean_reviews;
    d.mean_scores = +d.mean_scores;
    d.rent = +d.rent;
    d.housingPrice = +d.housingPrice;
    d.count = +d.count;
    return areaById.set(d['neighbourhood'].slice(0, 2), d);
  });

  var schemeByHousingPrice = () => {
    var colorScale = d3
      .scaleLinear()
      .domain([10000, 20000, 30000, 40000, 50000, 60000, 80000, 100000])
      .range(d3.schemeGreys[8]);

    var getColor = geoData => {
      var dataRow = areaById.get(geoData.properties.name.slice(0, 2));
      if (dataRow) {
        return colorScale(dataRow['housingPrice']);
      } else {
        console.log('no dataRow', geoData);
        return '#ccc';
      }
    };
    return getColor;
  };

  var schemeByLists = () => {
    var colorScale = d3
      .scaleLinear()
      .domain([100, 500, 1000, 1500, 2500, 5600])
      .range(d3.schemeGreys[6]);

    var getColor = geoData => {
      var dataRow = areaById.get(geoData.properties.name.slice(0, 2));
      if (dataRow) {
        return colorScale(dataRow['count']);
      } else {
        console.log('no dataRow', geoData);
        return '#ccc';
      }
    };
    return getColor;
  };

  var schemeByRent = () => {
    var colorScale = d3
      .scaleLinear()
      .domain([600, 700, 800, 900, 1000])
      .range(d3.schemeGreys[5]);

    var getColor = geoData => {
      var dataRow = areaById.get(geoData.properties.name.slice(0, 2));
      if (dataRow) {
        return colorScale(dataRow['rent']);
      } else {
        console.log('no dataRow', geoData);
        return '#ccc';
      }
    };
    return getColor;
  };

  var getColor = schemeByLists();
  if (plot === 'rent') {
    getColor = schemeByRent();
  } else if (plot === 'price') {
    getColor = schemeByHousingPrice();
  }

  const svg = d3.select('#mapChart');
  const g = d3.select('#mapChart .mapChart');

  const bjgeo = topojson.feature(geoData, geoData.objects.beijing_geo_full);

  var temp = { ...bjgeo };

  projection.fitSize([width, height], temp);

  if (region !== 'All') {
    temp.features = bjgeo.features.filter(i => i.properties.name === region);
    projection.fitSize([width, height], temp);
  } else if (region === 'All') {
    d3.selectAll('.arc').remove();
  }

  var tip = d3Tip()
    .attr('class', 'd3-tip')
    .offset([0, 0])
    .html(function (matchArea, area) {
      return (
        "<strong>District: </strong><span style='color:#fef7ea'>" +
        area.properties['name'] +
        "</span><br><strong> Lists: </strong><span style='color:#fef7ea'>" +
        matchArea['count'] +
        "</span><br><strong> Rent: </strong><span style='color:#fef7ea'>" +
        matchArea['rent'] +
        ' ¥ / day' +
        "</span><br><strong> Price: </strong><span style='color:#fef7ea'>" +
        matchArea['housingPrice'] +
        ' ¥ / m<sup>2</sup>' +
        "</span><br><strong> Attractions: </strong><span style='color:#fef7ea'>" +
        matchArea['num_spot']
      );
    });
  d3.select('.d3-tip').remove();
  svg.call(tip);

  const areas = g.selectAll('.area').data(temp.features);

  areas.exit().transition(t).attr('opacity', '0').remove();

  var new_area = areas
    .enter()
    .append('path')
    .attr('class', 'area')
    .on('mouseover', MouseOver)
    .on('mouseout', MouseOut);

  new_area
    .merge(areas)
    .transition(t)
    .attr('stroke', '#fef7ea')
    .attr('stroke-width', 0.6)
    .attr('d', path)
    .attr('id', d => d.properties['name'])
    .attr('fill', d => {
      return getColor(d);
    });

  function MouseOver(d) {
    var matchArea = beijing_area_stat.filter(item => {
      return (
        item['neighbourhood'].slice(0, 2) === d.properties['name'].slice(0, 2)
      );
    });
    d3.select(this).attr('stroke-width', 0.8);
    tip.show(matchArea[0], d, this);
  }

  function MouseOut(d) {
    var matchArea = beijing_area_stat.filter(item => {
      return (
        item['neighbourhood'].slice(0, 2) === d.properties['name'].slice(0, 2)
      );
    });
    d3.select(this).attr('stroke-width', 0.2);
    tip.hide(matchArea[0], d, this);
  }

  // Add animation to that path
  var mapChart = document.querySelector('.mapChart');
  anime({
    targets: mapChart,
    easing: 'easeInOutSine',
    opacity: [0, 1],
    duration: 1000,
    translateX: 0,
    direction: 'alternate',
    loop: false,
  });
  // var room_type_chart = document.querySelector("#room_type");
  // anime({
  //     begin: function (anim) {
  //         room_type_chart.style.display = "block";
  //     },
  //     targets: room_type_chart,
  //     easing: 'easeInOutSine',
  //     opacity: [0, 1],
  //     translateX: 0,
  //     duration: 2000,
  // });

  createHotSpots(handleSpot, hotSpot);
}

function createHotSpots(checked, hotSpot) {
  let t = d3.transition().duration(800).ease(d3.easeQuadOut);
  let g = d3.select('#mapChart .hotspots').attr('transform', 'translate(0,0)');

  if (checked) {
    var circles = g.selectAll('.circle').data(hotSpot);

    var new_circles = circles.enter().append('circle').attr('class', 'circle');

    new_circles
      .merge(circles)
      .transition(t)
      .style('opacity', 0.8)
      .attr('cx', d => projection([d['latitude'], d['longitude']])[0])
      .attr('cy', d => projection([d['latitude'], d['longitude']])[1])
      .attr('r', '3px')
      .attr('fill', 'rgb(255, 28, 0)');
  } else {
    g.selectAll('circle').transition(t).style('opacity', 0).remove();
  }
}
