import * as d3 from 'd3';
import anime from 'animejs';

const margin = {
    top: 100,
    bottom: 10,
    right: 10,
    left: 10,
};

let t = d3.transition()
    .duration(1000)
    .ease(d3.easeQuadOut);


export default function mkBarChart(preset, data, region, plot) {
    var { width, height, wrapperClassName, svgId, ChartClassName } = preset;
    d3.selectAll(`#${svgId}> *`).remove();

    var svg = d3.select(`#${svgId}`);

    const barWrapper = d3.select(`#${svgId}`).append("g").attr("transform", `translate(${margin.left},${margin.bottom})`)

    data.map(d => d.priceByRent = parseInt(d.housingPrice / d.rent))
    data.sort((a, b) => a.priceByRent - b.priceByRent)
    console.log(data)

    var x = d3.scaleLinear()
        .domain([0, d3.max(data, d => d.priceByRent) * 1.1])
        .range([margin.left, width - margin.right])

    var y = d3.scaleBand()
        .domain(d3.range(data.length))
        .rangeRound([margin.top, height - margin.bottom])
        .padding(0.1)

    var x_axis = d3.axisTop(x);
    var y_axis = d3.axisLeft(y);

    svg.append("g")
        .attr("transform", `translate(${margin.left},${height - margin.bottom - 10})`)
        .call(x_axis.ticks(width / 80, data.format))
        .call(g => g.selectAll(".tick text")
            .attr("x", 4)
            .attr("fill", "#3F3F40")
            .style("font-size", "1.2rem")
            .style("font-family", 'Coda')
            .attr("dy", 4))
        .call(g => g.selectAll(".tick line")
            .remove())
        .call(g => g.select(".domain").remove());

    svg.append("g")
        .attr("transform", `translate(${margin.left},-35)`)
        .call(y_axis.tickFormat(i => data[i].neighbourhood).tickSizeOuter(0))
        .call(g => g.selectAll(".tick text")
            .attr("x", -8)
            .attr("y", 2)
            .attr("fill", "#3F3F40")
            .style("font-family", 'Coda')
            .style("font-size", "1.3rem"))
        .call(g => g.select(".domain").remove())
        .call(g => g.selectAll(".tick line")
            .remove());

    svg.append("g")
        .attr("fill", "white")
        .attr("text-anchor", "end")
        .attr("font-family", "sans-serif")
        .attr("font-size", "1.5rem")
        .selectAll("text")
        .data(data)
        .join("text")
        .attr("x", d => x(d.priceByRent) - 4)
        .attr("y", (d, i) => (y(i) - 35) + (y.bandwidth() / 2))
        .attr("dy", "0.8em")
        .text(d => d.priceByRent);


    var bars = barWrapper.selectAll(`.${plot}Bar`)
        .data(data);

    bars.exit().remove();

    var new_bars = bars
        .enter()
        .append("rect")
        .attr("class", `${plot}Bar`)
        .attr("fill", d => {
            if (d.neighbourhood.slice(0, 2) === region.slice(0, 2)) {
                return "#df9827"
            } else {
                return "#3F3F40"
            }
        })
        .attr("stroke", "black")
        .attr("class", "bar");

    new_bars.merge(bars)
        .transition(t)
        .attr("opacity", "1")
        .attr("x", x(0))
        .attr("y", (d, i) => y(i) - 35)
        .attr("width", d => x(d.priceByRent) - x(0))
        .attr("height", y.bandwidth())


    d3.select(".barChartTitle").remove();
    d3.select(".barChartSubTitle").remove();
    d3.select(".barChartDes").remove();
    var title = "Housing Price vs Rent Price";
    var days = data.filter((d) => d.neighbourhood.slice(0, 2) === region.slice(0, 2))[0].priceByRent;
    var subtitle = `${days} Days Rent Avenue equals`;
    var des = `Housing Price per square metre in ${region}`;
    barWrapper.append("text")
        .attr("class", "barChartTitle")
        .attr("x", -50)
        .attr("y", (margin.top) / 2)
        .text(title)
        .style("font-size", "3em")
        .style("opacity", 1)
        .style("fill", "#3F3F40")
        .style("font-family", 'Coda')

    barWrapper.append("text")
        .attr("class", "barChartSubTitle")
        .attr("x", 230)
        .attr("y", (160))
        .text(subtitle)
        .style("font-size", "2.5em")
        .style("opacity", 1)
        .style("fill", "#3F3F40")
        .style("font-family", 'Coda')

    barWrapper.append("text")
        .attr("class", "barChartDes")
        .attr("x", 180)
        .attr("y", 200)
        .text(des)
        .style("font-size", "2em")
        .style("opacity", 1)
        .style("fill", "#3F3F40")
        .style("font-family", 'Coda')

    var priceChart = document.querySelector(`#${svgId}`);
    anime({
        targets: priceChart,
        easing: 'easeInOutSine',
        duration: 1000,
        translateX: 10,
        direction: 'alternate',
        loop: false
    });

}