import React, { Component } from 'react'
// import 'antd/dist/antd.css';
import { Select, Radio, Switch, Spin } from 'antd';
import { CloseOutlined, CheckOutlined } from '@ant-design/icons';
import './SideBar.css'

const { Option } = Select;
const region = ['All', '东城区', '丰台区', '大兴区', '密云区', '平谷区', '延庆区', '怀柔区', '房山区', '昌平区', '朝阳区', '海淀区', '石景山区', '西城区', '通州区', '门头沟区', '顺义区']

const radioStyle = {
    display: 'block',
    width: '150px',
    height: 'auto',
    lineHeight: '30px',
    margin: 0 + ' auto',
};

class SiderBar extends Component {
    componentDidMount() {
        var lodingScreen = document.querySelector(".ant-spin-nested-loading");
        var inputBar = document.querySelector(".sideBar");
        inputBar.style.opacity = 0.5;
        lodingScreen.style.opacity = 1;
        lodingScreen.style.display = 'block';
        setTimeout(() => {
            inputBar.style.opacity = 1;
            lodingScreen.style.opacity = 0;
            lodingScreen.style.display = 'none';
        }, 1000)
    }

    handleAreaChange = (value) => {
        this.props.selectRegion(value)
    }
    handleRadioChange = (e) => {
        var value = e.target.value;
        this.props.selectPlot(value)
    }
    handleHotSoptChange = (checked) => {
        this.props.selectSpot(checked)
    }

    render() {
        return (
            <div className="sideBarWrapper">
                <Spin tip="Loading..." size="large"> </Spin>
                <div className="sideBar">
                    <form>
                        {/* Icons From https://icons8.com/icon/pack/travel/cotton */}
                        <label htmlFor="switch"><img src="https://img.icons8.com/cotton/64/000000/beach-umbrella--v3.png" title="tourist attractions" height="50px" width="50px" /></label>
                        <Switch
                            checkedChildren={<CheckOutlined />}
                            unCheckedChildren={<CloseOutlined />}
                            onChange={this.handleHotSoptChange}
                            id="switch"
                            size="large"
                        />
                        <br />
                        <Select defaultValue="All" style={{ width: 120 }} onChange={this.handleAreaChange}>

                            {region.map((item) => {
                                return <Option value={item} key={item}><img src="https://img.icons8.com/wired/64/000000/region-code.png" height="26px" width="26px" /> {item}</Option>
                            })}
                        </Select>



                        <Radio.Group onChange={this.handleRadioChange} defaultValue="lists" buttonStyle="solid" style={radioStyle} size="large" >
                            <Radio.Button style={radioStyle} value="lists">Listings</Radio.Button>
                            <Radio.Button style={radioStyle} value="rent">Rent</Radio.Button>
                            <Radio.Button style={radioStyle} value="price">Property</Radio.Button>
                        </Radio.Group>

                    </form>
                </div>
            </div>
        )
    }
}
export default SiderBar;