import * as d3 from 'd3';
import anime from 'animejs';


const margin = {
    top: 100,
    bottom: 40,
    right: 10,
    left: 10,
};

let t = d3.transition()
    .duration(1000)
    .ease(d3.easeQuadOut);


var mkLineChart = (preset, data, region, plot) => {

    var { width, height, wrapperClassName, svgId, ChartClassName } = preset;
    d3.selectAll(`#${svgId}>*`).remove();


    const lineWrapper = d3.select(`#${svgId}`);


    data.forEach(item => { item.Date = new Date(item.Date) });
    data = data.filter(d => d.neighbourhood.slice(0, 2) === region.slice(0, 2));
    var line = d3.line()
        .x(d => x(d.Date))
        .y(d => y(d.price_x))
    // .curve(d3.curveMonotoneX); // apply smoothing to the line


    var x = d3.scaleUtc()
        .domain(d3.extent(data, d => d.Date))
        .range([margin.left, width - margin.right]);


    var y = d3.scaleLinear()
        .domain([d3.min(data, d => d.price_x), d3.max(data, d => d.price_x)]).nice()
        .range([height - margin.bottom, 200]);


    var x_axis = d3.axisBottom(x);
    var y_axis = d3.axisLeft(y);

    // Append X Axis to the chart
    lineWrapper.append("g")
        .attr("transform", `translate(${margin.left},${height - margin.bottom - 10})`)
        .call(x_axis.tickFormat(d3.timeFormat('%b')).tickSizeOuter(0))
        .call(g => g.selectAll(".tick text")
            .attr("x", 4)
            .attr("fill", "#3F3F40")
            .style("font-size", "1.6rem")
            .style("font-family", 'Coda')
            .attr("dy", 25))
        .call(g => g.select(".domain")
            .attr("stroke", "#959496"))
        .call(g => g.selectAll(".tick line")
            .attr("stroke", "#959496"));

    // Append Y Axis to the chart
    lineWrapper.append("g")
        .attr("transform", `translate(${margin.left},${0})`)
        .call(y_axis.ticks(10))
        .call(g => g.selectAll(".tick text")
            .attr("x", -8)
            .attr("fill", "#3F3F40")
            .style("font-family", 'Coda')
            .style("font-size", "1.5rem"))
        .call(g => g.select(".domain")
            .remove())
        .call(g => g.selectAll(".tick line")
            .attr("stroke", "#959496"));

    // Draw the Line 
    lineWrapper.append("path")
        .datum(data)
        .attr("transform", `translate(${margin.left},${0})`)
        .attr("opacity", "1")
        .attr("fill", "none")
        .attr("stroke", "#A2787E")
        .attr("stroke-width", 3)
        .attr("stroke-linejoin", "round")
        .attr("stroke-linecap", "round")
        .attr("d", line)
        .call(path => {
            let length = path.node().getTotalLength();
            path.attr("stroke-dasharray", length);
        }
        )

    /// Annotation

    d3.select(".lineChartTitle").remove();
    d3.select(".lineChartSubTitle").remove();
    d3.select(".lineChartYaxistext").remove();
    var text = {
        title: "Airbnb Daily Price From Apr 2020",
        maxPrice: "Max Price: ",
        minPrice: "Min Price: ",
    }
    var maxPrice = d3.max(data, d => d.price_x);
    var minPrice = d3.min(data, d => d.price_x);

    lineWrapper.append("text")
        .attr("class", "lineChartTitle")
        .attr("x", 0)
        .attr("y", (margin.top) / 2)
        .text(text.title)
        .style("font-weight", "bold")
        .style("font-size", "3em")
        .style("opacity", 1)
        .style("fill", "#3F3F40")
        .style("font-family", 'Coda');

    lineWrapper.append("text")
        .attr("class", "lineChartSubTitle")
        .attr("x", 0)
        .attr("y", (margin.top))
        .text(region + " " + text.maxPrice)
        .style("font-size", "2em")
        .style("opacity", 1)
        .style("fill", "#3F3F40")
        .style("font-family", 'Coda')
        .append("tspan")
        .text(maxPrice)
        .style("font-weight", "bold")
        .append("tspan")
        .text(" | " + text.minPrice)
        .style("font-weight", "normal")
        .append("tspan")
        .text(minPrice)
        .style("font-weight", "bold")


    lineWrapper.append("text")
        .attr("class", "lineChartYaxistext")
        .attr("x", 0)
        .attr("y", margin.top + 30)
        .text("¥/day")
        .style("font-size", "1.6em")
        .style("opacity", 0.8)
        .style("fill", "#959496")
        .style("font-family", 'Coda');

    // Animation
    var lineChart = document.querySelector(`#${svgId}`);
    anime({
        targets: lineChart,
        easing: 'easeInOutSine',
        duration: 1000,
        translateX: 10,
        direction: 'alternate',
        loop: false
    });
    var linePath = document.querySelector(`#${svgId} > path`);
    anime({
        targets: linePath,
        strokeDashoffset: [anime.setDashoffset, 0],
        easing: 'easeInOutSine',
        translateX: 10,
        duration: 1000,
        delay: function (el, i) { return i * 150 },
    });

}

export default mkLineChart;