import React, { Component } from 'react'
import mkSvg from '../../../util/mkSvg.js';
import mkLineChart from '../DailyLineChart/mkLineChart';
import mkReviewsBarChart from '../ScoreBarChart/mkBarChart';
import mkPriceByRentBarChart from '../PriceByRentBarChart/mkPriceByRentBarChart';


import './Chart.css';

export default class Chart extends Component {

    componentDidMount() {
        var { preset, region, plot, dailyPrice, beijing_area_stat } = this.props;

        mkSvg(preset)
        if (plot === "rent") {
            mkLineChart(preset, dailyPrice, region, plot)
        } else if (plot === 'lists') {
            mkReviewsBarChart(preset, beijing_area_stat, region, plot)
        } else if (plot === 'price') {
            mkPriceByRentBarChart(preset, beijing_area_stat, region, plot)
        }


    };
    componentDidUpdate() {

        var { preset, region, plot, dailyPrice, beijing_area_stat } = this.props;

        setTimeout(() => {
            if (plot === "rent") {
                mkLineChart(preset, dailyPrice, region, plot)
            } else if (plot === 'lists') {
                mkReviewsBarChart(preset, beijing_area_stat, region, plot)
            } else if (plot === 'price') {
                mkPriceByRentBarChart(preset, beijing_area_stat, region, plot)
            }
        })

    }
    render() {


        return (
            <div className={this.props.preset.wrapperClassName} style={{ height: 80 + '%', width: 95 + "%" }}>
            </div>
        )
    }

}