// Components import
import React, { Component } from 'react';
import Map from '../Map/Map';
import Headline from '../Headline/Headline';

// Style import
import 'antd/dist/antd.css';
import './App.css';
import { Layout } from 'antd';

// import data
import geoData from '../App/bj_sp_topo';
import hotSpot from '../App/hostspot';
import beijing_area_stat from '../App/beijing_area_stat';
import dailyReginalPrice from '../App/daily_price_area';

const { Header, Footer, Content } = Layout;

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      geoData: geoData,
    };
  }

  render() {
    const { geoData } = this.state;
    return (
      <Layout className='container'>
        <Header className='title'>
          <Headline></Headline>
        </Header>
        <Layout className='main'>
          <Content>
            <Map
              geoData={geoData}
              beijing_area_stat={beijing_area_stat}
              hotSpot={hotSpot}
              dailyPrice={dailyReginalPrice}
            ></Map>
          </Content>
        </Layout>
        <Footer className='footer'>
          <p className='copyright-text'>
            Copyright &copy; 2020 All Rights Reserved by
            <a
              href='https://github.com/Ruoyu-Klaus'
              style={{ color: '#fef7ea' }}
            >
              {' '}
              Ruoyu Wang
            </a>
            .
          </p>
        </Footer>
      </Layout>
    );
  }
}

export default App;
