import React, { Component } from 'react';
import MapDraw from '../Map/draw/index.js';
import SiderBar from '../SideBar/SideBar';
import SideChart from '../SideChart/SideChart';
import { Row, Col } from 'antd';
import './Map.css';

class Map extends Component {
  constructor(props) {
    super(props);
    this.state = {
      region: 'All',
      plot: 'lists',
    };
  }
  // a selection of regions
  selectRegion = region => {
    this.setState({ region: region });
  };
  // handle color scheme change
  selectPlot = plot => {
    this.setState({ plot: plot });
  };
  // check if switch on for showing attraction's locations on the map
  selectSpot = checked => {
    this.setState({ SpotChecked: checked });
  };

  render() {
    const { geoData, beijing_area_stat, hotSpot, dailyPrice } = this.props;
    return (
      <div className='contentWrapper'>
        <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
          <Col className='gutter-row' xs={24} sm={4} md={6} lg={6} xl={6}>
            <SiderBar
              selectRegion={this.selectRegion}
              selectPlot={this.selectPlot}
              selectSpot={this.selectSpot}
            ></SiderBar>
          </Col>
          <Col className='gutter-row' xs={24} sm={4} md={6} lg={8} xl={8}>
            <MapDraw
              geoData={geoData}
              beijing_area_stat={beijing_area_stat}
              region={this.state.region}
              plot={this.state.plot}
              hotSpot={hotSpot}
              handleSpot={this.state.SpotChecked}
            ></MapDraw>
          </Col>
          <Col className='gutter-row' xs={24} sm={16} md={12} lg={10} xl={10}>
            <SideChart
              region={this.state.region}
              plot={this.state.plot}
              dailyPrice={dailyPrice}
              beijing_area_stat={beijing_area_stat}
            ></SideChart>
          </Col>
        </Row>
      </div>
    );
  }
}
export default Map;
